import { Stack, styled, TextField, Typography } from "@mui/material"
import React, { ReactNode } from "react"

interface ITextField {
  title: string
  label: string
  variant?: "standard" | "filled" | "outlined"
  type?: "password" | "text"
  id: string
  childrenStack?: ReactNode
}

/**
 *   TextField  
 *
 * @param {string} title -    ()
 * @param {string} label -    
 * @param {string} variant -   
 * @param {string} type -   
 * @param {string} id -  ID,   
 * @param {ReactNode} childrenStack -  ,    
 * @param {object} otherProps -   
 * @returns {React.FC<ITextField>} - FC
 */
export const BaseTextField: React.FC<ITextField> = ({
  title,
  label,
  variant = "outlined",
  type = "text",
  id,
  childrenStack,
  ...otherProps
}) => (
  <Stack direction="row" spacing={3} alignItems="center" {...otherProps}>
    <Typography variant="body1" children={title} />
    <STextField id={id} label={label} variant={variant} type={type} />
    {childrenStack}
  </Stack>
)

const STextField = styled(TextField)({
  width: "360px",
})
