import React, { Fragment } from "react"

import { SPaper, SHeader, STypography, SFooter } from "./_styles"

interface ICover {
  title: string
  children: React.ReactNode
  headerChildren?: React.ReactNode
  footerChildren?: React.ReactNode
  showBorderTitle?: boolean
  heightInherit?: "heightInherit"
}

/**
 *   
 *
 * @param {string} title - 
 * @param {React.ReactNode} children -  
 * @param {React.ReactNode} headerChildren -    
 * @param {boolean} isBorderedTitle -   :  / 
 * @param {React.ReactNode} footerChildren -    
 * @param {string} heightInherit -       
 * @returns {React.FC<ICover>} - FC
 */
export const Cover: React.FC<ICover> = ({
  title,
  children,
  headerChildren = null,
  showBorderTitle = false,
  footerChildren = null,
  heightInherit,
}) => (
  <Fragment>
    <SPaper variant={heightInherit}>
      <SHeader
        sx={{
          borderBottomColor: showBorderTitle
            ? "transparent"
            : (theme): string => theme.palette.primary.main,
        }}
      >
        <STypography variant="h6">{title}</STypography>
        {headerChildren}
      </SHeader>
      {children}
    </SPaper>
    {footerChildren && <SFooter>{footerChildren}</SFooter>}
  </Fragment>
)
